import Modal from "../../../Ui/Modal/Modal";
import style from "./BankDetail.module.css";
import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useEffect, useState } from "react";
import CustomSelectDepartment from "../../../Ui/Select/CustomSelectDepartment";
import CreateButton from "../../../Ui/Button/CreateButton";
import SelectMasterOtherFeeHead from "../FeesStructure/Structure/SelectMasterOtherFeeHead";
import FormInputView from "../../../Ui/Input/FormInputView";
import InstituteDepartment from "../../InstituteDepartment/InstituteDepartment";

const BankSelectApplicableModal = ({
  fid,
  onToggle,
  libraryId,
  transportId,
  hostelId,
  setApplicablFor,
  applicableFor,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    flow: "",
    flow_id: "",
    flow_name: "",
    depart_arr: [],
    heads_list: [],
  });
  const [step, setStep] = useState("");

  useEffect(() => {
    setState(applicableFor);
  }, []);

  const onSelectApplicable = (val) => {
    if (val === "Hostel") {
      setState({
        flow: "Hostel",
        flow_id: hostelId,
        flow_name: "Hostel",
        depart_arr: [],
        heads_list: [],
      });
    } else if (val === "Transport") {
      setState({
        flow: "Transport",
        flow_id: transportId,
        flow_name: "Transport",
        depart_arr: [],
        heads_list: [],
      });
    } else if (val === "Library") {
      setState({
        flow: "Library",
        flow_id: libraryId,
        flow_name: "Library",
        depart_arr: [],
        heads_list: [],
      });
    } else if (val === "Society") {
      setState({
        flow: "Society",
        flow_id: null,
        flow_name: "Society",
        depart_arr: [],
        heads_list: [],
      });
    } else if (val === "Internal Department") {
      setState({
        flow: "Department",
        flow_id: "",
        flow_name: "",
        depart_arr: [],
        heads_list: [],
      });
    } else if (val === "Miscellaneous") {
      setState({
        flow: "Miscellaneous",
        flow_id: "",
        flow_name: "Miscellaneous",
        depart_arr: [],
        heads_list: [],
      });
    } else {
    }
  };

  // const onSelectInternalDepartment = (val) => {
  //   setState((prev) => ({
  //     ...prev,
  //     flow_id: val?._id,
  //     flow_name: val?.dName,
  //     depart_arr: [...new Set([...prev.depart_arr, val?._id])],
  //   }));
  // };

  const onSelect = () => {
    setApplicablFor(state);
    onToggle();
  };

  const onCloseStep = () => {
    setStep("");
  };

  const onSelectHeads = (data) => {
    // console.info("data", data);
    setState((prev) => ({
      ...prev,
      heads_list: data,
    }));
  };
  const onSelectDepartment = (val) => {
    if (state.depart_arr?.includes(val?._id)) {
      let ft = state?.depart_arr?.filter((dt) => dt !== val?._id);
      setState((prev) => ({
        ...prev,
        flow_id: ft?.[0],
        depart_arr: ft,
      }));
    } else {
      setState((prev) => ({
        ...prev,
        flow_id: val?._id,
        flow_name: val?.dName,
        depart_arr: [...prev.depart_arr, val?._id],
      }));
    }
  };

  // console.info("sdfbh", state);
  return (
    <>
      {step === 1 ? (
        <SelectMasterOtherFeeHead
          onClose={onCloseStep}
          fid={fid}
          feeHeadId={state.heads_list}
          setFeeHeadId={onSelectHeads}
          isIdOnly
        />
      ) : step === 2 ? (
        <InstituteDepartment
          onClose={onCloseStep}
          onMultiSelect={onSelectDepartment}
          title={t("select_department")}
          instituteId={instituteId}
          whichType=""
          isMultiSelect
          selectList={state.depart_arr}
          onDone={onCloseStep}
        />
      ) : (
        <Modal onClose={onToggle}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper
              customStyle={{
                paddingBottom: "1rem",
              }}
            >
              <CustomSelectDepartment
                selectLabel={t("applicable_for")}
                selectedValue={t("applicable_for_placeholder")}
                options={[
                  "Internal Department",
                  "Society",
                  "Transport",
                  "Library",
                  "Hostel",
                  "Miscellaneous",
                ]}
                onClick={onSelectApplicable}
                isFull
                defalutValue={state.flow}
                viewAs="NORMAL_FILTER"
              />
              {state.flow === "Department" && (
                <>
                  {/* <CustomSelectDepartment
                  selectLabel={t("select_internal_department")}
                  selectedValue={t("select_internal_department_placeholder")}
                  options={instituteAllDepartment ?? []}
                  onClick={onSelectInternalDepartment}
                  isFull
                  viewAs="NORMAL_FILTER"
                  defalutValue={
                    state.depart_arr?.length > 1
                      ? `${state.depart_arr?.length} department selected`
                      : state.flow_name
                  }
                /> */}
                  <FormInputView
                    label={`${t("selected_department")} : `}
                    value={state.depart_arr?.length}
                  />
                  <CreateButton
                    label="select_internal_department"
                    onAction={() => setStep(2)}
                  />
                </>
              )}

              {state.flow === "Miscellaneous" ? (
                <>
                  <FormInputView
                    label={`${t("selected_fee_head")} : `}
                    value={state.heads_list?.length}
                  />
                  <CreateButton
                    label="select_other_fee_master"
                    onAction={() => setStep(1)}
                  />
                </>
              ) : null}
              <button
                className={style.applicable_btn}
                onClick={onSelect}
                style={{
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                <div>{t("select")}</div>
              </button>
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}
    </>
  );
};

export default BankSelectApplicableModal;
